import {ACTIONS} from '../action-types';

const initialState = {
  cart: [],
  booking: {},
  loader:false

};

const GernelReducers = (state = initialState, action) => {
  switch (action.type) {
    case ACTIONS.SET_CART:
      return {
        ...state,
        cart: action.data,
      };
      case ACTIONS.SET_BOOKING:
      return {
        ...state,
        booking: action.data,
      };
      case ACTIONS.SET_LOADER:
        return {
          ...state,
          loader: action.data,
        };
    

    default:
      return state;
  }
};

export default GernelReducers;
